body {
  font-family: "Raleway", "Source Sans Pro";
  background-color: #16202A;
  height: 100vh;
  width: 100vw;
  max-height: 100vh;
  max-width: 100vw;
  overflow: hidden;
  background-image: url('../images/twit-bird.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
body .container {
  height: 100%;
  text-align: center;
  padding-top: calc(50vh - 8rem);
}
body .container .item.search-bar {
  margin: 0 auto;
  width: 70%;
}
body .container .item.search-bar form input.search-box {
  box-sizing: border-box;
  background: none;
  border: none;
  width: 100%;
  font-size: 3rem;
  border-bottom: 5px solid #1da1f3;
  text-align: center;
  color: #1da1f3;
}
body .container .item.search-bar form input.search-box:focus {
  outline: none;
}
body .container .item.search-bar form input.search-box::-webkit-input-placeholder {
  color: #1da1f3;
  opacity: 0.3;
  font-weight: normal;
}
body .container .item.search-bar form input.search-box::-moz-placeholder {
  color: #1da1f3;
  opacity: 0.3;
  font-weight: normal;
}
body .container .item.search-bar form input.search-box:-ms-input-placeholder {
  color: #1da1f3;
  opacity: 0.3;
  font-weight: normal;
}
body .container .item.search-bar form input.search-box::-ms-input-placeholder {
  color: #1da1f3;
  opacity: 0.3;
  font-weight: normal;
}
body .container .item.search-bar form input.search-box::placeholder {
  color: #1da1f3;
  opacity: 0.3;
  font-weight: normal;
}
body .container .item.search-bar form .search-button {
  box-sizing: border-box;
  width: 100%;
  font-weight: bolder;
  background-color: #1da1f3;
  color: rgba(0, 0, 0, 0);
  font-size: 1.5rem;
  text-align: center;
  outline: none;
  border: none;
  padding: 0px;
  margin: 0px;
  height: 0px;
  -webkit-transition: height 0.25s linear;
  transition: height 0.25s linear;
}
body .container .item.search-bar form .search-box:valid ~ .search-button {
  height: 75px;
  color: #16202A;
  -webkit-transition: height 0.25s linear;
  transition: height 0.25s linear;
}
body .container .item.app-name {
  font-size: 4rem;
  margin-bottom: 4rem;
  color: #1da1f3;
}
